import '../css/SignUp.module.scss';
import React, { useRef, useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import styles from "../css/SignUp.module.scss"

const SignUp = () => {
    let comNameRef = useRef();
    let nameRef = useRef();
    let idRef = useRef();
    let pwRef = useRef();
    let repwRef = useRef();

    const [msg1, setMsg1] = useState();
    const [msg2, setMsg2] = useState();
    const [msg3, setMsg3] = useState();
    const [color3, setColor3] = useState('red');
    const [able, setAble] = useState(true);
    const [btnColor, setBtnColor] = useState();
    const [check, setCheck] = useState({a:0, b:0, c:0});

    const navigate = useNavigate();

    //id  
    const check_id = () => {
        let id = document.getElementById('id').value;

        //  
        const SC = ["!", "@", "#", "$", "%", "~", "^", "&", "*", "(", ")", "-", "+", "="];
        let check_SC = 0;


        // 
        if (id.length === 0) {
            setMsg1("");
            setCheck({...check, a: 0});
        }
        else {
            // 1. 6   12    
            if (id.length < 6 || 12 < id.length) {
                setMsg1(" 6 , 12  ");
                setCheck({...check, a: 0});
            }
            else {
                // 2. 6 ~ 12   
                setMsg1("");
                for (let i = 0; i < SC.length; i++) {
                    if (id.indexOf(SC[i]) !== -1) {
                        check_SC = 1;
                    }
                }
                if (check_SC > 0) {
                    setMsg1('   .');
                    setCheck({...check, a: 0});
                } else {
                    setMsg1("");
                    setCheck({...check, a: 1});
                }
            }
        }
        check_final();
    }


    //    
    const check_pw = () => {

        let pw = document.getElementById('pw').value;
        const SC = ["!", "@", "#", "$", "%", "~"];
        let check_SC = 0;


        if (pw.length === 0) {
            setMsg2("");
            setCheck({...check, b: 0});
        } else {
            if (pw.length < 8 || pw.length > 16) {
                setMsg2(" 8 , 16  .");
                setCheck({...check, b: 0});
            } else {
                setMsg2("");
                for (var i = 0; i < SC.length; i++) {
                    if (pw.indexOf(SC[i]) !== -1) {
                        check_SC = 1;
                    }
                }
                if (check_SC === 0) {
                    setMsg2("  .");
                    setCheck({...check, b: 0});
                } else {
                    setMsg2("");
                    setCheck({...check, b: 1});
                }
            }


        }
        check_final();

    }

    const check_same = () => {
        let pw = document.getElementById('pw2').value;

        if (pw.length === 0) {
            setMsg3("");
            setCheck({...check, c: 0});
        } else {
            if (document.getElementById('pw').value !== '' && pw !== '') {
                if (document.getElementById('pw').value === pw) {
                    setColor3('blue');
                    setMsg3(" .");
                    setCheck({...check, c: 1});
                }
                else {
                    setMsg3("  .");
                    setColor3('red');
                    setCheck({...check, c: 0});
                }
            }
        }

        check_final();
    }

    const check_final = () => {
        console.log(check)
        //  ,      
        if (document.getElementById("comName").value && document.getElementById("name").value && check.a && check.b && check.c) {
            setAble(false);
            setBtnColor('#000000');
        }else{
            setAble(true);
        }
    }

    const handleSingup = (e) => {
        e.preventDefault();

        axios
            .post("http://www.stock-manage-api.p-e.kr/signup/admin", {
                "comName": comNameRef.current.value,
                "name": nameRef.current.value,
                "id": idRef.current.value,
                "password": pwRef.current.value,
            })
            .then((res) => {
                console.log(res);
                if (res.status === 200) {
                    navigate("/");
                } else {
                    alert(res.error);
                    comNameRef.current.value = "";
                    nameRef.current.value = "";
                    idRef.current.value = "";
                    pwRef.current.value = "";
                    repwRef.current.value = "";
                }

            })
            .catch((e) => {
                console.error(e);
            });

    }

    return (
        <div className={styles.body}>
            <h1 align="center"> Inventory - Manager</h1>
            <div id={styles.mainBox}>
                <form className={styles.registForm} onInput={check_final}>

                    <h3></h3>

                    <p className={styles.name}></p>
                    <div className={styles.textform}>
                        <input ref={comNameRef} id="comName" className={styles.textin} type="text" placeholder="ex)" />
                    </div>
                    <p className={styles.name}></p>
                    <div className={styles.textform}>
                        <input ref={nameRef} id="name" className={styles.textin} type="text" placeholder="ex)" />
                    </div>

                    <p className={styles.name}></p>
                    <div className={styles.textform}>
                        <input ref={idRef} className={styles.textin} type="text" id="id" placeholder="6  12  /"
                            onInput={check_id} /><span className={styles.warn} id={styles.check1}>{msg1}</span>
                    </div>

                    <p className={styles.name}></p>
                    <div className={styles.textform}>
                        <input ref={pwRef} className={styles.textin} type="password" id="pw" placeholder="8  16  //"
                            onInput={check_pw} /><span className={styles.warn} id={styles.check2}>{msg2}</span>
                    </div>
                    <p className={styles.name}> </p>
                    <div className={styles.textform}>
                        <input ref={repwRef} className={styles.textin} type="password" id="pw2" placeholder="  "
                            onInput={check_same} /><span className={styles.warn} id={styles.check3} style={{ color: color3 }}>{msg3}</span>
                    </div>

                    <p className={styles.name}>   </p>
                    <p id={styles.explain}>   .</p>
                    <div className={styles.selection}>
                        <label><input type="radio" name="employee" value="admin" /></label>
                        <label><input type="radio" name="employee" value="normal" /></label>
                    </div>
                    <input id={styles.completion} type="submit" className={styles.btn} value="" onClick={handleSingup} style={{ backgroundColor: btnColor }} disabled={able} />
                </form>
            </div>
        </div>
    )
}

export default SignUp;