import { useRef } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import "../css/Login.module.scss";
import { Link } from 'react-router-dom';
import styles from "../css/Login.module.scss"

const Login = () => {
    const idRef = useRef();
    const pwRef = useRef();

    const navigate = useNavigate();

    const handleLogin = (e) => {
        e.preventDefault();

        if (idRef.current.value === "" || idRef.current.value === undefined) {  //id  
            alert(" ");
            idRef.current.focus();
            return false;
        }
        if (pwRef.current.value === "" || pwRef.current.value === undefined) {  //pw  
            alert(" ");
            pwRef.current.focus();
            return false;
        }

        console.log(
            "LoginForm:window.sessionStorage(login_id) =>",
            window.sessionStorage.getItem("id")
        );

        axios
            .post("http://www.stock-manage-api.p-e.kr/login", {
                "id": idRef.current.value,
                "password": pwRef.current.value,
            })
            .then((res) => {
                console.log("handleLogin =>", res);
                if (res.data.code === 200) {
                    window.sessionStorage.setItem("token", res.data.token); //  key : id , value : idRef.current.value 
                    // sessionsStorage   , localStorage 
                    navigate("/MainPage", false);
                } else {
                    alert(",   .");
                    idRef.current.value = "";
                    pwRef.current.value = "";
                    navigate("/");
                }
            })
            .catch((e) => {
                console.error(e);
            });
    };

    return (
        <div className={styles.body}>
            <div className={styles.container}>
                <h1>Welcome</h1>
                <form>
                    <div className={styles.formControl}>
                        <input type="text" name="id" ref={idRef} placeholder="" required />
                    </div>

                    <div className={styles.formControl}>
                        <input type="password" name="password" ref={pwRef} placeholder="" required />
                    </div>

                    <button type="submit" className={styles.btn} onClick={handleLogin}></button>

                    <p className={styles.text}> ? <Link to="/SignUp"></Link></p>
                </form>
            </div>
        </div>
    )
}

export default Login;