import { ResponsiveBar } from '@nivo/bar'
import {useLocation} from 'react-router-dom'
// make sure parent container have a defined height when using
// responsive component, otherwise height will be 0 and
// no chart will be rendered.
// website examples showcase many properties,
// you'll often use just a few of them.
const Graph = () => {
    const location = useLocation();

    const item = location.state.prop;
    const name = location.state.name;

    let sor = item.map(obj=>({date:Object.keys(obj)[0], value:Object.values(obj)[0]}))

    console.log(sor)
    return (
        <div style={{ width: '1000px', height: '500px', margin: '0 auto', marginTop: '140px' }}>
            <ResponsiveBar
                /**
                 * chart  
                 */
                data={sor}
                /**
                 * chart   key ( )
                 */
                keys={["value"]}
                /**
                 * keys  index key ( )
                 */
                indexBy="date"
                /**
                 * chart margin
                 */
                margin={{ top: 50, right: 130, bottom: 50, left: 60 }}
                /**
                 * chart padding (bar )
                 */
                padding={0.3}
                /**
                 * chart 
                 */
                colors={'brown'} //   
                // colors={{ scheme: 'nivo' }} // nivo     
                /**
                 * color  
                 */
                colorBy="id" //  keys   
                // colorBy="indexValue" // indexBy    
                theme={{
                    /**
                     * label style (bar  )
                     */
                    labels: {
                        text: {
                            fontSize: 14,
                            fill: '#000000',
                        },
                    },
                    /**
                     * legend style (default     key )
                     */
                    legends: {
                        text: {
                            fontSize: 12,
                            fill: '#000000',
                        },
                    },
                    axis: {
                        /**
                         * axis legend style (bottom, left  )
                         */
                        legend: {
                            text: {
                                fontSize: 20,
                                fill: '#000000',
                            },
                        },
                        /**
                         * axis ticks style (bottom, left  )
                         */
                        ticks: {
                            text: {
                                fontSize: 16,
                                fill: '#000000',
                            },
                        },
                    },
                }}
                /**
                 * axis bottom 
                 */
                axisBottom={{
                    tickSize: 5, //      
                    tickPadding: 5, // tick padding
                    tickRotation: 0, // tick 
                    legend: name, // bottom 
                    legendPosition: 'middle', //  
                    legendOffset: 40, //  chart 
                }}
                /**
                 * axis left 
                 */
                axisLeft={{
                    tickSize: 5, //      
                    tickPadding: 5, // tick padding
                    tickRotation: 0, // tick 
                    legend: 'count', // left 
                    legendPosition: 'middle', //  
                    legendOffset: -60, //  chart 
                }}
                /**
                 * label    width
                 */
                labelSkipWidth={36}
                /**
                 * label    height
                 */
                labelSkipHeight={12}
                /**
                 * bar  
                 */
                /**
                 * legend  (default     key )
                 */
                legends={[
                    {
                        dataFrom: 'keys', //   
                        anchor: 'bottom-right', // 
                        direction: 'column', // item  
                        justify: false, // ,   justify  
                        translateX: 120, // chart X 
                        translateY: 0, // chart Y 
                        itemsSpacing: 2, // item 
                        itemWidth: 100, // item width
                        itemHeight: 20, // item height
                        itemDirection: 'left-to-right', // item   
                        itemOpacity: 0.85, // item opacity
                        symbolSize: 20, // symbol ( ) 
                        effects: [
                            {
                                //    (hover item opacity 1 )
                                on: 'hover',
                                style: {
                                    itemOpacity: 1,
                                },
                            },
                        ],
                    },
                ]}
            />
        </div>
    )
}

export default Graph;